.info_warp {
  width: 100%;
  height: 100%;
}

.info_warp .i_w_bg {
  overflow: hidden;
  overflow-y: hidden;
  width: 100%;
  height: 100%;
  position: relative;
}

.info_warp .i_w_bg .bg {
  height: 70%;
}

.info_warp .i_w_bg .header_item {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
}

.info_warp .i_w_bg .header_item .user_item_info {
  background: #261f0e;
  border-radius: .3rem;
  display: flex;
  justify-content: space-between;
  height: 1rem;
}

.info_warp .i_w_bg .header_item .user_item_info img {
  width: .8rem;
  height: .8rem;
}

.info_warp .i_w_bg .header_item .user_item_info .item_t {
  display: flex;
  flex-direction: column;
  color: #fff;
}

.info_warp .i_w_bg .header_item .user_item_info .item_t .name {
  font-size: .18rem;
}

.info_warp .i_w_bg .header_item .user_item_info .item_t .p_num {
  font-size: .18rem;
}

.info_warp .i_w_bg .header_item .user_item_info .item_r {
  color: #fff;
  font-size: .4rem;
}

.info_warp .i_w_bg .header_item .u_img img {
  width: .8rem;
  height: .8rem;
}

.info_warp .i_w_bg .header_item .u_img span {
  font-size: .5rem;
  color: #fff;
}

.info_warp .i_w_bg .act_pro {
  width: 24%;
  height: 3.6rem;
  position: absolute;
  top: 0;
  right: 0;
  margin-top: 3rem;
  background: #ef5983;
  border-radius: .3rem;
  padding: .2rem;
}

.info_warp .i_w_bg .act_pro .a_tit {
  font-size: .4rem;
  line-height: .8rem;
  color: #fff;
}

.info_warp .chat {
  position: absolute;
  left: 0;
  bottom: 3rem;
  z-index: 3;
  height: 3rem;
  width: 100%;
}

.info_warp .chat .my-carousel .v-item {
  height: 1.25rem;
  line-height: 1.25rem;
  padding-left: 0.266667rem;
  font-size: .45rem;
  color: #fff;
}

.info_warp .item_footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 3;
  display: flex;
  justify-content: space-between;
  background-color: #261f0e;
}

.info_warp .item_footer img {
  width: .8rem;
  height: .8rem;
}

.info_warp .item_footer span {
  font-size: .45rem;
  color: #fff;
}